<template>
  <v-container>
    <v-card>
      <v-container grid-list-md mb-0>
        <v-layout row wrap style="margin:10px">
          <v-flex class="text-left">
            <v-icon size="90" color="blue accent-3">mdi-cake</v-icon>
          </v-flex>
          <v-flex class="text-right mr-4">
            <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
            </v-text-field>
          </v-flex>
          <v-flex class="text-right mr-4">
            <h1>DESSERT CATALOGUE</h1>
              <v-btn depressed dark rounded style="text-transform: none !important;" color="blue accent-3"
                @click="dialog = true">
                <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                ADD DESSERT
              </v-btn>
          </v-flex>
        </v-layout>
        <v-container class="my-5">
          <v-layout row wrap>
            <v-flex xs12 sm6 md4 lg3 v-for="item in desserts" :key="item.id" :search="keyword">
              <v-card flat class="text-xs-center ma-3 elevation-5">
                <v-card-text>
                 <div class=" blue--text text-uppercase font-weight-bold title">{{ item.name }} </div>
                  <div class=" text-uppercase font-weight-bold">Calories :{{ item.calories }} (g) </div>
                  <div class=" text-uppercase font-weight-bold">Fat     : {{ item.fat }} (g) </div>
                  <div class=" text-uppercase font-weight-bold">Carbs   : {{ item.carbs }} (g) </div>
                  <div class=" text-uppercase font-weight-bold">Protein : {{ item.protein }} (g) </div>
                </v-card-text>
                <v-card-actions class="blue darken-3">

                  <v-btn  color="transparent elevation-0">
                    <v-icon class="mr-0" color="white" @click="edititem(item)">mdi-pencil</v-icon>
                    <div class="white--text">EDIT</div>
                  </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="transparent elevation-0">
                    <v-icon class="mr-0" color="white" @click="deleteitem(item.id)">mdi-delete</v-icon>
                    <div class="white--text">DELETE</div>
                  </v-btn>
                  <!-- <div v-if = "role == 'user' " >
                             <v-btn color="transparent elevation-0" style="margin-left:35%">
                                <div class="white--text">ADD TO CART</div>
                                <v-icon class="mr-0" color="white" @click="addCart(item.id)">mdi-cart</v-icon>
                            </v-btn>
                            </div> -->
                </v-card-actions>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-container>
    </v-card>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="blue--text title"> DRINKS PROFILE </span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="editedItem.name" label="Dessert name"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
              </v-col>
            </v-row>
          </v-container>
          <small class="red--text font-weight-bold text-uppercase">*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close()">Close</v-btn>
          <v-btn color="blue darken-1" text @click="save()">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
      {{ text }}
      <v-btn dark @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </v-container>
</template>
<script>
import {dessertsRef} from "../firebase";
export default {
  data: () => ({
    i: 0,
    cek: -1,
    dialog: false,
    keyword:'',
    search: "",
    desserts: [],
    check: [],
    color: null,
    snackbar:false,
    text: '',
    editedIndex: -1,
    editedItem: {
      name: "",
      calories: "",
      fat: 0,
      carbs: 0,
      protein: 0,
    },
    defaultItem: {
      name: "",
      calories: "",
      fat: 0,
      carbs: 0,
      protein: 0,
    }
  }),
  firebase: {
    desserts: dessertsRef
  },
  computed: {
    formTitle() {
      return this.cek === -1 ? "New Item" : "Edit Item";
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  methods: {
    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    clear() {
      this.editedItem = {};
      this.editedItem.name = "";
      this.editedItem.calories = 0;
      this.editedItem.fat = 0;
      this.editedItem.carbs = 0;
      this.editedItem.protein = 0;
      this.cek = -1;
      this.i = 0;
    },
    // methods untuk save
    save() {
      if (this.cek > -1) {
        dessertsRef.child(this.editedIndex).set({
          name: this.editedItem.name,
          calories: this.editedItem.calories,
          fat: this.editedItem.fat,
          carbs: this.editedItem.carbs,
          protein: this.editedItem.protein,
        });
        this.clear();
      } else {
        dessertsRef.push({
          name: this.editedItem.name,
          calories: this.editedItem.calories,
          fat: this.editedItem.fat,
          carbs: this.editedItem.carbs,
          protein: this.editedItem.protein
        });
        this.clear();
      }
      this.check = [];
      this.close();
    },
    edititem(item) {
      this.editedIndex = item[".key"];
      this.cek = 0;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    deleteitem(item) {
      confirm("Are you sure you want to delete this item?") &&
        drinksRef.child((item[".key"])).remove();
    },
    deleteCheck() {
      if (this.check.length == 0) {
        alert("Belum ada dessert yang dipilih")
      } else {
        for (this.i = 0; this.i < this.check.length; this.i++) {
          confirm("Are you sure you want to delete this item?") && dessertsRef.child(this.check[this.i][".key"]).remove();
        }
      }
      this.check = [];
    }
  }
};
</script>